import React, {
  useState,
  useRef,
  useEffect,
  Fragment,
  useCallback,
} from "react";
import { Typography, Grid, Paper } from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import * as actions from "../../store/actions";
import { useTranslation } from "react-i18next";
import MaterialTable from "material-table";
import IconTextNotice from "../../components/common/IconTextNotice";
import Ttext from "../../components/common/Ttext";
import { arSA } from "date-fns/locale";
import { tableIcons } from "../../util/tableIcons";
import Input from "../../components/Inputs/Input";
import PageError from "../../components/common/PageError";
import { format } from "date-fns";
import i18n from "../../i18n";
import TableSkeleton from "../../components/common/TableSkeleton";
import GroupRoundedIcon from "@material-ui/icons/GroupRounded";
import IosSwitch from "../../components/Inputs/IosSwitch";

const tableHeaders = [
  {
    title: <Ttext ns="common" text="number" />,
    field: "user_id",
    // width: 1,
  },
  {
    title: <Ttext ns="common" text="name" />,
    field: "name",
  },
  {
    title: <Ttext ns="common" text="mobile" />,
    field: "mobile",
    render: (row) => (
      <Typography className="ltr" variant="body2">
        {row.mobile}
      </Typography>
    ),
  },
  {
    title: <Ttext ns="users" text="orders_total" />,
    field: "orders",
    render: (row) => row.orders,
  },
  {
    title: <Ttext ns="users" text="purchases_total" />,
    field: "total",
    render: (row) => row.total,
  },
  {
    title: <Ttext ns="users" text="points" />,
    field: "points",
  },
  {
    title: <Ttext ns="users" text="date_added" />,
    field: "date_added",
    render: (row) =>
      format(new Date(row.date_added), "yyyy MMMM dd hh:mm:ss aaaa", {
        locale: i18n.language === "ar" ? arSA : "",
      }),
  },
];

const initialQuery = {
  q: "",
  page: 0,
  perPage: 10,
  sort: "date_added",
  direction: "asc",
};
let timeout;

const Users = ({ location }) => {
  const [query, setQuery] = useState(initialQuery);
  const users = useSelector((state) => state.users);
  const [mounted, setMounted] = useState(false);
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const tableRef = useRef();
  const hasPagination = users.users.pagination;

  const handleQuery = (value, validity, name) => {
    setQuery((prev) => {
      return {
        ...prev,
        [name]: value,
      };
    });
  };

  const fetchUsers = useCallback(async () => {
    try {
      await dispatch(actions.getUsers(query));
    } finally {
      setMounted(true);
    }
  }, [dispatch, query]);

  useEffect(() => {
    timeout = setTimeout(() => {
      fetchUsers();
    }, 500);
    return () => clearTimeout(timeout);
  }, [query, fetchUsers]);

  const handleSwitchState = (data) => {
    const id = data.user_id;
    const status = data.status > 0 ? 0 : 1;
    dispatch(actions.switchUserStatus(id, status));
  };

  return (
    <Fragment>
      <Typography variant="h4" className="py">
        {t("users:users_list")}
      </Typography>
      <Grid container spacing={2} className="py">
        <Grid item sm={6} xs={12}>
          <Input
            label={t("common:searchhhh")}
            getResult={handleQuery}
            name="q"
            initialValue={query.q}
          />
        </Grid>
      </Grid>

      {!mounted ? (
        <Paper>
          <TableSkeleton row={2} />
        </Paper>
      ) : !users.loading && users.errors ? (
        <PageError
          text={users.errors.message || t("common:uncommon_err")}
          onTryAgain={fetchUsers}
        />
      ) : (
        <MaterialTable
          icons={tableIcons}
          columns={tableHeaders}
          tableRef={tableRef}
          data={users.users.users}
          components={{
            Toolbar: (props) => " ",
            Actions: (props) => {
              const user = props.data;
              return (
                <IosSwitch
                  state={user.status}
                  onChange={() => handleSwitchState(user)}
                  name="status"
                  loading={users.itemLoading}
                />
              );
            },
          }}
          actions={[
            {
              icon: () => null,
              onClick: () => null,
              hidden: true,
            },
          ]}
          options={{
            detailPanelColumnAlignment: "right",
            actionsColumnIndex: -1,
            draggable: false,
            emptyRowsWhenPaging: false,
            search: false,
            pageSize: query.perPage,
            pageSizeOptions: [5, 10, 20, 50],
            thirdSortClick: false,
            showTitle: false,
          }}
          onChangeRowsPerPage={(size) => handleQuery(size, true, "perPage")}
          onOrderChange={(orderBy, direction) => {
            const sort = tableHeaders[orderBy].field;
            setQuery((prev) => {
              return {
                ...prev,
                sort: sort,
                direction: direction,
              };
            });
          }}
          totalCount={hasPagination ? hasPagination.totalCount : 0}
          page={hasPagination ? hasPagination.currentPage : 1}
          onChangePage={(page) => handleQuery(page, true, "page")}
          localization={{
            header: {
              actions: "",
            },
            body: {
              emptyDataSourceMessage: users.loading
                ? t("common:loadingggg")
                : !users.loading &&
                  !users.errors && (
                    <IconTextNotice
                      Icon={GroupRoundedIcon}
                      text={t("users:no_users")}
                    />
                  ),
              dateTimePickerLocalization: i18n.language === "ar" ? arSA : "",
            },
            pagination: {
              labelDisplayedRows:
                "{from}-{to} " + t("common:from") + " {count}",
              labelRowsSelect: t("common:perpage"),
            },
          }}
          isLoading={users.loading}
        />
      )}
    </Fragment>
  );
};

export default Users;
